import React, { useEffect } from 'react';
import { Row, Col, Spin } from 'antd';
import defaultStyles from './TaskDetail.less';
import { ArrowRightOutlined } from '@ant-design/icons';
import { RESOURCE_AGENT_STATUS_STORAGE_KEY, RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, ResourceAgentStatus } from '@/contexts/constants';

const TaskDetail: React.FC<{ readyStatus: number, setReadyStatus: React.Dispatch<React.SetStateAction<number>>, styles?: React.CSSProperties }> = ({ readyStatus, setReadyStatus, styles = defaultStyles }) => {
    useEffect(() => {
        let intervalId: NodeJS.Timeout | null = null;
        if (readyStatus < ResourceAgentStatus.resourceSolutionGenerated) {
            intervalId = setInterval(() => {
                setReadyStatus((prevReadyStatus) => {
                    let newReadyStatus = prevReadyStatus + 1;
                    if (intervalId && newReadyStatus >= ResourceAgentStatus.resourceSolutionGenerated) {
                        clearInterval(intervalId);
                        newReadyStatus = ResourceAgentStatus.resourceSolutionGenerated;
                    }
                    // update status stored in localStorage
                    localStorage.setItem(RESOURCE_AGENT_STATUS_STORAGE_KEY, newReadyStatus.toString());

                    // update status timestamp stored in localStorage
                    let statusTimestamps = new Map();
                    const statusTimestampsStr = localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY);
                    if (statusTimestampsStr && statusTimestampsStr !== '[]') {
                        statusTimestamps = new Map(JSON.parse(statusTimestampsStr));
                    }
                    statusTimestamps.set(newReadyStatus.toString(), new Date().toLocaleString());
                    localStorage.setItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, JSON.stringify(Array.from(statusTimestamps.entries())));
                    return newReadyStatus;
                });
            }, 1500);
        }

        return () => {
            if (intervalId) {
                clearInterval(intervalId);
            }
        };
    }, []);

    return (
        <Row gutter={24} align="middle" justify="center" style={{ flexWrap: 'nowrap' }}>
            <Col style={{ padding: 0 }}>
                <div className={styles.boxLeft}>
                    <div className={styles.boxTitle} style={readyStatus === ResourceAgentStatus.bssPlanGenerated ? {} : { color: '#bfc8b7' }}>Business Order</div>
                    <div className={styles.boxText} style={readyStatus === ResourceAgentStatus.bssPlanGenerated ? {} : { color: '#bfc8b7' }}>
                        {readyStatus >= ResourceAgentStatus.bssPlanGenerated && (
                            <>
                                <strong>Operator Resources​</strong><br />
                                <span>• 5G Medical Private Network​</span><br />
                                <span>• 200TB IDC Encrypted Storage​​</span><br />
                                <span>• AI Cloud Resources​​​</span><br />
                                <strong>Cloud Vendor Services​</strong><br />
                                <span>• Tenc Cloud AI Imaging Platform​</span><br />
                                <span>• Model Fine - Tuning Service​</span><br />
                                <strong>Value - Added Services​</strong><br />
                                <span>• Privacy Computing Service​</span><br />
                                <span>• Energy - Saving Optimization Service​​</span><br />
                            </>
                        )}
                    </div>
                </div>
            </Col>
            <Col>
                <div className={styles.flowContainer}>
                    <ArrowRightOutlined className={styles.flowArrow} />
                    {readyStatus < ResourceAgentStatus.resourceMatched ? (
                        readyStatus === ResourceAgentStatus.bssPlanGenerated ? (
                            <Spin><div className={styles.flowStepNotReady}>Resource Matching</div></Spin>
                        ) : <div className={styles.flowStepNotReady}>Resource Matching</div>
                    ) : <div className={styles.flowStep}>Resource Matching</div>}
                    <ArrowRightOutlined className={readyStatus < 1 ? styles.flowArrowNotReady : styles.flowArrow} />
                    {readyStatus < ResourceAgentStatus.resourceVerificated ? (
                        readyStatus === ResourceAgentStatus.resourceMatched ? (
                            <Spin><div className={styles.flowStepNotReady}>Resource Verification</div></Spin>
                        ) : <div className={styles.flowStepNotReady}>Resource Verification</div>
                    ) : <div className={styles.flowStep}>Resource Verification</div>}
                    <ArrowRightOutlined className={readyStatus < 2 ? styles.flowArrowNotReady : styles.flowArrow} />
                    {readyStatus < ResourceAgentStatus.resourceSolutionGenerated ? (
                        readyStatus === ResourceAgentStatus.resourceVerificated ? (
                            <Spin><div className={styles.flowStepNotReady}>Resource Solution Generation</div></Spin>
                        ) : <div className={styles.flowStepNotReady}>Resource Solution Generation</div>
                    ) : <div className={styles.flowStep}>Resource Solution Generation</div>}
                    <ArrowRightOutlined className={readyStatus < ResourceAgentStatus.resourceSolutionGenerated ? styles.flowArrowNotReady : styles.flowArrow} />
                </div>
            </Col>
            <Col style={{ padding: 0 }}>
                <div className={styles.boxRight}>
                    <div className={styles.boxTitle} style={readyStatus < ResourceAgentStatus.resourceSolutionGenerated ? { color: '#bfc8b7' } : {}}>Resource Service Order</div>
                    <div className={styles.boxText} style={readyStatus < ResourceAgentStatus.resourceSolutionGenerated ? { color: '#bfc8b7' } : {}}>
                        {readyStatus >= ResourceAgentStatus.resourceSolutionGenerated && (
                            <>
                                <strong>Service Content</strong><br />
                                <span>• Complete the deployment of the 5G private network and slice configuration within 3 working days, and simultaneously enable the intelligent power - saving function.</span><br />
                                <span>• Allocate to the AI Cloud Resources of the Qing Shan Green Data Center, a "Green Data Center", and achieve high - speed interconnection via SD - WAN.</span><br />
                                <span>• Complete the docking between the cloud platform and the PACS system within 5 working days.</span><br />
                                <span>• Provide 7×24 - hour operation and maintenance, with a fault response time of ≤5 minutes.</span><br />
                                <span>• Provide an energy - efficiency report every quarter to optimize resource utilization.</span><br />
                                <strong>Performance Requirements</strong><br />
                                <span>{"• 5G Private Network: Latency < 10ms, bandwidth ≥ 1Gbps, packet loss rate < 0.01%."}</span><br />
                                <span>• AI Cloud: Inference response ≤ 3 seconds, training throughput ≥ 100 images per minute.</span><br />
                                <span>• Business Availability ≥ 99.9%, data storage reliability ≥ 99.9999%.</span><br />
                                <span>• Energy - saving Indicator: The overall system energy consumption is reduced by more than 25% compared to traditional solutions.</span><br />
                                <strong>Application Location</strong><br />
                                <span>• No. 88 Keji Road, Qingxiu District, Qingshan City.</span><br />
                                <strong>Resource Requirements</strong><br />
                                <span>• Data Center: The Qing Shan Green Data Center certified as a "Green Data Center"; real - time intelligent energy - saving solution.</span><br />
                                <span>• Cloud Resource Pool: Qing Shan Green Cloud Resource Pool.</span><br />
                                <span>• Computing Resources: 8 NVIDIA A100 GPUs, 64 - core CPUs, 256GB of memory.</span><br />
                                <span>• Storage Resources: 200TB IDC Encrypted Storage (SSD solid - state drives).</span><br />
                                <span>• Network Technologies: 5G SA standalone networking (including i ntelligent power - saving technology), SD - WAN interconnection.</span><br />
                                <span>• Protection Requirements: Dual - active disaster recovery, daily incremental data backup, and weekly full - volume off - site backup.</span><br />
                            </>
                        )}
                    </div>
                </div>
            </Col>
        </Row>
    );
};

export default TaskDetail;
